<template>
  <div :style="threeBoxStyle">
    <div class="text-subtitle-2" :style="hoverObjectStyle">
      {{ currentObjectName }}
    </div>
    <div id="visualizer" :style="style.canvas"></div>
    <div class="text-subtitle-2" :style="bottomObjectStyle">
      <!-- <span class="mr-3">{{
        customerData.size[0].toUpperCase()
      }}</span> -->
      <v-icon
        v-if="customerData.temperature === 'cold'"
        :style="{ color: 'skyblue' }"
        >mdi-snowflake</v-icon
      >
      <v-icon
        v-if="
          customerData.temperature === 'hot' ||
          customerData.temperature === 'less hot'
        "
        :style="{ color: 'orange' }"
        >mdi-fire</v-icon
      >
      <v-icon
        v-if="customerData.temperature === 'hot'"
        :style="{ color: 'orange' }"
        >mdi-fire</v-icon
      >
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import * as THREE from "three";

export default {
  name: "ThreeBox",
  data: function () {
    return {
      animationDuration: 1000,
      drinks: this.allDrinks,
      meshGroup: null,
      meshArr: null,
      canvas: null,
      plane: null,
      modifier: null,
      renderer: null,
      scene: null,
      camera: null,
      multiplier: 10,
      currentTween: null,
      threeBoxStyle: {
        display: "flex",
        flexDirection: "row",
        justifyContent: "center",
      },
      hoverObjectStyle: {
        position: "absolute",
        top: "2rem",
      },
      bottomObjectStyle: {
        position: "absolute",
        bottom: "1rem",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
      },

      // for interactivity
      fallBackColor: null,
      raycaster: null,
      pointer: null,
      currentObjectName: null,
      currentObject: null,
      currentObjectColor: null,
    };
  },
  computed: {
    style: function () {
      return {
        canvas: {
          height: "30rem",
          width: "100%",
          margin: "0 auto",
          backgroundColor: "rgba(0, 0, 0, 0.87)",
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
        },
      };
    },
    chosenDrink: function () {
      var that = this;
      var drinkID = that.customerData.base;

      return that.drinks[drinkID];
    },
    ...mapState(["appData", "customerData"]),
  },
  watch: {
    "customerData.size": {
      handler: function (val, old) {
        this.createStack();
      },
    },
    "customerData.baseDrink.config": {
      deep: true,
      handler: function (val, old) {
        this.createStack();
      },
    },
    "customerData.temperature": {
      handler: function (val, old) {
        if (this.meshArr) {
          this.meshArr.forEach((mesh) =>
            this.setEmissiveColor(mesh.material, true),
          );
        }
      },
    },
  },
  mounted: function () {
    /*
     *  KEEP THIS ORDER
     */

    // set up canvas
    this.canvas = document.querySelector("#visualizer");
    var canvasHeight = this.canvas.offsetHeight;
    var canvasWidth = this.canvas.offsetWidth;

    // set up renderer
    this.renderer = new THREE.WebGLRenderer({
      antialias: true,
    });
    this.renderer.setPixelRatio(window.devicePixelRatio);
    this.renderer.setSize(canvasWidth, canvasHeight, true);
    this.renderer.setClearColor(0xffffff, 1);
    this.renderer.shadowMap.enabled = true;

    // add to dom
    this.canvas.appendChild(this.renderer.domElement);

    // set up scene
    this.scene = new THREE.Scene();
    // this.scene.fog = new THREE.Fog( 0x050505, 2000, 3500 );

    // init scene
    this.createStack(false);

    // set up lights
    const light1 = new THREE.SpotLight(0xffffff);
    light1.intensity = 1;
    light1.penumbra = 20;
    light1.position.set(0, 2500, 2500);

    // set up shadow
    light1.castShadow = true;
    light1.shadow.mapSize.width = 10000;
    light1.shadow.mapSize.height = 10000;
    light1.shadow.camera.near = 500;
    light1.shadow.camera.far = 4000;
    light1.shadow.camera.fov = 30;
    light1.shadow.radius = 10;

    light1.lookAt(this.meshArr[0].position);

    this.scene.add(light1);
    // this.scene.add(new THREE.SpotLightHelper(light1, 5));

    const light2 = new THREE.SpotLight(0xffffff);
    light2.position.set(2500, 2000, 1000);
    light2.intensity = 0.5;

    this.scene.add(light2);
    // this.scene.add(new THREE.SpotLightHelper(light2, 5));

    // create a plane
    var planeGeometry = new THREE.PlaneGeometry(5000, 5000);
    var planeMaterial = new THREE.MeshPhongMaterial({ side: THREE.DoubleSide });
    var plane = new THREE.Mesh(planeGeometry, planeMaterial);
    plane.rotation.x = THREE.Math.degToRad(90);
    plane.position.y = -1;
    // plane.castShadow = true;
    plane.receiveShadow = true;
    this.scene.add(plane);

    // set up camera
    // this.camera = new THREE.PerspectiveCamera(
    //   40,
    //   canvasWidth / canvasHeight,
    //   0.1,
    //   10000,
    // );
    // this.camera.position.set(900, 900, 500);
    this.camera = new THREE.OrthographicCamera(
      canvasWidth / -1.25,
      canvasWidth / 1.25,
      canvasHeight / 1.25,
      canvasHeight / -1.25,
      0,
      10000,
    );
    this.camera.position.set(3000, 3000, 3000);

    this.camera.lookAt(
      this.meshArr[0].position.x,
      this.meshArr[0].position.y + 130,
      this.meshArr[0].position.z,
    );

    // helpers
    // this.scene.add(new THREE.AxesHelper(250));

    // init scene & render
    this.renderer.render(this.scene, this.camera);
    this.initRayCaster();

    window.addEventListener("resize", this.onWindowResize);
    document.addEventListener("mousemove", this.onPointerMove, false);
  },
  destroyed() {
    window.removeEventListener("resize", this.onWindowResize);
  },
  methods: {
    initRayCaster: function () {
      // set up interactivity
      this.raycaster = new THREE.Raycaster();
      this.pointer = new THREE.Vector2();
    },
    onPointerMove: function (event) {
      event.preventDefault();

      this.pointer.x = (event.offsetX / this.canvas.offsetWidth) * 2 - 1;
      this.pointer.y = -(event.offsetY / this.canvas.offsetHeight) * 2 + 1;

      this.raycaster.setFromCamera(this.pointer, this.camera);
      this.intersect = this.raycaster.intersectObjects(this.meshArr);

      if (this.intersect.length > 0) {
        if (this.intersect[0].object !== this.currentObject) {
          // remove current object styling
          if (this.currentObject) {
            this.currentObjectName = null;
            this.currentObject.material.emissive.setHex(this.fallBackColor);
            this.currentObject.material.emissiveIntensity = this.fallBackIntensity;
          }

          // set new object as current
          this.currentObject = this.intersect[0].object;
          this.currentObjectName = this.currentObject.name;
          this.currentObject.material.emissive.setHex(0xffffff);
          this.currentObject.material.emissiveIntensity = 0.25;

          this.renderer.render(this.scene, this.camera);
          this.canvas.style.cursor = "pointer";
        }
      } else {
        // clear
        if (this.currentObject) {
          this.currentObject.material.emissive.setHex(this.fallBackColor);
          this.currentObject.material.emissiveIntensity = this.fallBackIntensity;

          this.currentObject = null;
          this.currentObjectColor = null;
          this.currentObjectName = null;

          this.renderer.render(this.scene, this.camera);
          this.canvas.style.cursor = "default";
        }
      }
    },
    calcVolume: function (callback = false) {
      /*
       * 1. Get drink size
       * 2. Get pump-size
       * 3. Find ratio to drink size
       *
       * USE ML
       */

      var info = this.appData;
      // var drinkSize = info.drinkSize[this.customerData.size].ml;
      var pumpSize = info.pumpSize.ml;

      var ingredients = this.customerData.ingredients;

      // general ingredients
      for (let type in ingredients) {
        if (type == "flavors" || type == "sweeteners") {
          for (let subtype in ingredients[type]) {
            ingredients[type][subtype].volume =
              ingredients[type][subtype].quantity * pumpSize;
          }
        } else {
          ingredients[type].volume = ingredients[type].quantity * pumpSize;
        }
      }

      if (callback) {
        callback();
      }
    },
    getCylID: function (cylName) {
      var id = this.meshArr.findIndex(function (item) {
        return item.name == cylName;
      });

      return id;
    },
    onWindowResize: function () {
      this.canvas = document.querySelector("#visualizer");

      this.renderer.setSize(
        this.canvas.offsetWidth,
        this.canvas.offsetHeight,
        true,
      );

      this.camera.left = this.canvas.offsetWidth / -1.25;
      this.camera.right = this.canvas.offsetWidth / 1.25;
      this.camera.top = this.canvas.offsetHeight / 1;
      this.camera.bottom = this.canvas.offsetHeight / -1.5;
      this.camera.updateProjectionMatrix();

      this.renderer.render(this.scene, this.camera);
    },
    makeCyl: function (cylName, cylHeight, cylColor = "#FFFFFF") {
      var that = this;

      if (cylHeight > 0) {
        var geo = new THREE.CylinderGeometry(150, 150, cylHeight, 40);
        var mat = new THREE.MeshPhongMaterial({
          color: cylColor,
        });

        // TODO: unnecessary runs global
        this.setEmissiveColor(mat, false, true);

        var mesh = new THREE.Mesh(geo, mat);

        mesh.castShadow = true;
        // mesh.receiveShadow = true;

        mesh.name = cylName;
        mesh.position.y = cylHeight / 2;

        return mesh;
      } else {
        return null;
      }
    },
    setEmissiveColor: function (material, render = false, global = true) {
      const temp = this.customerData.temperature;
      let color = "";

      const colors = {
        hot: 0xf44336,
        "less hot": 0xf06292,
        cold: 0x2196f3,
      };

      material.emissive.setHex(colors[temp]);
      material.emissiveIntensity = 0.25;

      if (global) {
        this.fallBackColor = colors[temp];
        this.fallBackIntensity = 0.25;
      }

      render && this.renderer.render(this.scene, this.camera);
    },
    createStack: function (render = true) {
      var that = this;
      var arr = [];

      this.scene.remove(this.scene.getObjectByName("thisGroup"));

      const sizes = {
        small: 0.6,
        medium: 1,
        large: 1.2,
      };

      const multiplier = sizes[that.customerData.size];

      var ingredients = that.appData.ingredients;

      if (
        that.customerData.baseDrink.tags.includes("americano") ||
        that.customerData.baseDrink.tags.includes("black_coffee")
      ) {
        arr.push(
          that.makeCyl(
            "water",
            that.customerData.baseDrink.config.espresso * 3 * multiplier,
            that.appData.options.water.color,
          ),
        );
      } else {
        arr.push(
          that.makeCyl(
            "milk",
            that.customerData.baseDrink.config.espresso * 3 * multiplier,
            that.appData.options.milk.color,
          ),
        );
      }

      arr.push(
        that.makeCyl(
          "espresso",
          that.customerData.baseDrink.config.espresso * 1.5 * multiplier,
          ingredients.espresso.color,
        ),
        that.makeCyl(
          "chocolate",
          that.customerData.baseDrink.config.flavors.chocolate * multiplier,
          ingredients.flavors[0].color,
        ),
        that.makeCyl(
          "caramel",
          that.customerData.baseDrink.config.flavors.caramel * multiplier,
          ingredients.flavors[1].color,
        ),
        that.makeCyl(
          "vanilla",
          that.customerData.baseDrink.config.flavors.vanilla * multiplier,
          ingredients.flavors[2].color,
        ),
        that.makeCyl(
          "sugarFreeVanilla",
          that.customerData.baseDrink.config.flavors.sugarFreeVanilla *
            multiplier,
          ingredients.flavors[3].color,
        ),
        that.makeCyl(
          "sugar",
          that.customerData.baseDrink.config.sweeteners.sugar * multiplier,
          ingredients.sweeteners[0].color,
        ),
        that.makeCyl(
          "stevia",
          that.customerData.baseDrink.config.sweeteners.stevia * multiplier,
          ingredients.sweeteners[1].color,
        ),
      );

      var newArr = arr.filter((x) => x != null);

      var count = newArr.length;
      var group = new THREE.Group();
      group.name = "thisGroup";

      // loop through for respective heights
      for (var i = 0; i < count; i++) {
        if (i == 0) {
          group.add(newArr[i]);
          continue;
        }

        for (var x = 0; x < i; x++) {
          // console.log(newArr[i].name, newArr[x].name);
          newArr[i].position.y += newArr[x].geometry.parameters.height;
        }

        group.add(newArr[i]);
      }

      this.scene.add(group);
      if (render) {
        // that.camera.lookAt(that.meshArr[that.meshArr.length - 1].position);
        that.renderer.render(that.scene, that.camera);
      }
      this.meshArr = newArr;

      return group;
    },
    updateStack: function (id) {
      // get ratio
      //   const newHeight =
      //     this.meshArr[0].geometry.parameters.height +
      //     this.meshArr[0].geometry.parameters.height * 0.1;

      //   const ratio = this.meshArr[0].geometry.parameters.height / newHeight + 1;

      //   // resize and render
      //   this.meshArr[0].scale.y = this.meshArr[0].scale.y + 1;
      //   this.renderer.render(this.scene, this.camera);

      //   console.log(this.meshArr[0]);

      //   // update values and reset
      //   this.meshArr[0].scale.y = 1;
      //   this.meshArr[0].geometry.parameters.height = newHeight;

      console.log(this.meshArr[1]);
      this.scaleCyl(0, this.meshArr[0].geometry.parameters.height + 10, false);

      let totalHeight = 0;
      this.meshArr.forEach((c) => {
        c.position.y = totalHeight;
        console.log(totalHeight);
        totalHeight += c.geometry.parameters.height;
      });

      this.renderer.render(this.scene, this.camera);
    },
    scaleCyl: function (cylID, cylHeight, render = false, callback = false) {
      var meshes, renderer, scene, camera;
      var that = this;

      meshes = that.meshArr;
      renderer = that.renderer;
      scene = that.scene;
      camera = that.camera;

      /*
       *
       *
       *  Order of Operation
       *  1. Get current scale
       *  2. Get current height
       *  3. use new height to find new scale
       *
       *  Visual of Ratio
       *
       *
       *	oldScale     oldHeight
       *	--------  =  ---------
       *	newScale     newHeight
       *
       *  newScale = (oldScale / oldHeight) * newHeight
       *  newHeight = newScale / (oldScale / oldHeight)
       *
       *
       */

      var oldHeight = meshes[cylID].geometry.parameters.height;
      var newHeight = cylHeight > 0 ? cylHeight : 0.000001;
      var ogScale = meshes[cylID].scale.y;
      var newScale = (ogScale / oldHeight) * newHeight;
      // var tween;

      if (newHeight != oldHeight) {
        meshes[cylID].scale.y = newScale;
        meshes[cylID].position.y = newHeight / 2;
        meshes[cylID].geometry.parameters.height = newHeight;
        render && that.renderer.render(that.scene, that.camera);
      }
    },
  },
};
</script>
